import React from 'react';
import Header from './components/header';
import Input from './components/input';
import Qualifications from './components/qualifications';
import './App.css';

class App extends React.Component {
  state = {
    qualifications: [
      {id:0}
      // {id:1},
      // {id:2},
      // {id:3},
      // {id:4},
      // {id:5},
      // {id:6}
    ]
  }

 render () {return (
  <React.Fragment>
    <Header />
    <div className="d-flex flex-column align-items-center" >
    <Input />
    <Qualifications
    qualifications = {this.state.qualifications}
    addQualification = {this.addQualification}
    deleteQualification = {this.deleteQualification}
    />
    </div>
  </React.Fragment>
  );
}

findQualificationId = () => {
  const qualifications = this.state.qualifications;
  for(let i=0; i<qualifications.length+1; i++){
    if(!qualifications.some(c => c.id===i)) return i;
  }
}

addQualification = () => {
  const qualifications = this.state.qualifications;
  let id = this.findQualificationId();
  qualifications.push({id:id});
  this.setState({qualifications});
}

deleteQualification = qualificationId => {
  const qualifications = this.state.qualifications.filter(c => c.id !== qualificationId);
  this.setState({ qualifications });
};

}

export default App;
