import React, { Component } from 'react';
import Qualification from './qualification';

class Qualifications extends Component {
    render() { 
        const {qualifications, addQualification, deleteQualification} = this.props;
        return <div className="container m-4 border rounded d-grid ">
                <h2 className="title text-center my-2">Qualifications</h2>
                {qualifications.map(qualification =>(
                    <Qualification
                        key={qualification.id}
                        deleteQualification = {deleteQualification}
                        id={qualification.id}
                    />
                ))}
                <button className="btn btn-outline-secondary mb-2 btn-block" onClick={addQualification}>+</button>
        </div>;
    }
}
 
export default Qualifications;